import {CONSTANTS, checkForError, LOGGING} from '@devsnc/library-opws-mapping-shared';
import {actionTypes} from '@servicenow/ui-core';
import {createHttpEffect} from '@servicenow/ui-effect-http';

import {SYMBOLS} from '../symbols';
import {t} from 'sn-translate';

const {COMPONENT_DOM_READY} = actionTypes;
const ACTION_CATEGORY_LAUNCH_APP_LABEL = t('Launch app');
const ACTION_CATEGORY_REMEDIATION_WORKFLOW_LABEL = t('Run remediation');
const RUN_ACTION_LABEL = t('Run Action');


const ACTION_TYPES = {
	ALERT_ACTIONS_REQUESTED: 'ALERT_ACTIONS#REQUESTED',
	ALERT_ACTIONS_STARTED: 'ALERT_ACTIONS#STARTED',
	ALERT_ACTIONS_SUCCEEDED: 'ALERT_ACTIONS#SUCCEEDED',
	ALERT_ACTIONS_FAILED: CONSTANTS.ACTION_TYPES.NOC_ERROR_OCCURRED,
	ALERT_ACTIONS_FLOW_EXECUTION_STARTED: 'ALERT_ACTIONS#FLOW_EXECUTION_STARTED'
};


const setCardsFilter = coeffects => {
	const {
		updateState,
		action: {payload: {value: cardsFilter}}
	} = coeffects;
	updateState({cardsFilter});
};

const getAlertActions = createHttpEffect('/api/now/em_actions/getManualActionsForAlert', {
	method: 'GET',
	queryParams: ['alertId'],
	startActionType: ACTION_TYPES.ALERT_ACTIONS_STARTED,
	successActionType: ACTION_TYPES.ALERT_ACTIONS_SUCCEEDED,
	errorActionType: ACTION_TYPES.ALERT_ACTIONS_FAILED
});

const createCard = (cardData, cardType, cardId, cardLabel) => {
	//const icon = cardType === SYMBOLS.CARD_TYPES.REMEDIATION_WORKFLOW ? 'wrench-outline' : '';
	let icon, label;
	if (cardType === SYMBOLS.CARD_TYPES.REMEDIATION_WORKFLOW) {
		icon = '';
		label = ACTION_CATEGORY_REMEDIATION_WORKFLOW_LABEL;
	} else {
		icon = 'wrench-outline';
		label = ACTION_CATEGORY_LAUNCH_APP_LABEL;
	}

	return {
		cardData: {...cardData},
		cardType,
		cardId,
		cardLabel,
		cardTagline: {icon, label},
		cardMenuItems: [{
			...cardData,
			cardType,
			cardMenuItemType: SYMBOLS.CARD_MENU_ITEM_TYPES.RUN_ACTION,
			label: RUN_ACTION_LABEL
		}]
	};
};

const getVariables = (alertId) => {
	return {alertId};
};

const transformToCards = (result) => {
	const {manualRemediationData, toolsForAlert} = result;
	toolsForAlert.sort((a, b) =>  b.displayName.toUpperCase() > a.displayName.toUpperCase() ? -1 : 0);
	manualRemediationData.sort((a, b) => b.workflowName.toUpperCase() > a.workflowName.toUpperCase() ? -1 : 0);

	const remediation = manualRemediationData.map((currentValue, index) => createCard(
		currentValue,
		SYMBOLS.CARD_TYPES.REMEDIATION_WORKFLOW,
		`remediation_${index}`,
		currentValue.workflowName));

	const launch = toolsForAlert.map((currentValue, index) =>
		createCard(
			currentValue,
			SYMBOLS.CARD_TYPES.LAUNCH_APP,
			`launch_${index}`,
			currentValue.displayName));

	return [...remediation, ...launch];
};

const setAlertActions = {
	effect: (coeffects) => {
		LOGGING.log('setAlertActions');
		const {
			dispatch,
			updateState,
			action: {payload}
		} = coeffects;

		const errors = checkForError({payload, dispatch, caller: 'setAlertActions'});
		if (errors) {
			LOGGING.error('setAlertActions failed', errors);
			return;
		}


		const {result} = payload;
		const items = transformToCards(result);
		LOGGING.info('alertActionsEffect', {
			caller: 'setAlertActions',
			extra: {items}
		});
		updateState({items, componentState: SYMBOLS.COMPONENT_STATE.READY});

	},
	modifier: {
		name: 'debounce',
		delay: 200
	}
};


const init = ({properties, dispatch}) => {
	const packageJson = require('../../../package');
	LOGGING.info(`${packageJson.name} package version : ${packageJson.version}`, {
		force: true, caller: 'init',
		extra: {packageJson}
	});

	const {sysId} = properties;
	if (!sysId) {
		LOGGING.error('sn-em-ai-action expect to get as argument alertId');
		return;
	}
	dispatch(ACTION_TYPES.ALERT_ACTIONS_REQUESTED, getVariables(sysId) , {stopPropagation: true});
};

const actionHandlers = {
	[COMPONENT_DOM_READY]: init,
	[CONSTANTS.ACTION_TYPES.SEARCH_TERM_CHANGED]: setCardsFilter,
	[ACTION_TYPES.ALERT_ACTIONS_REQUESTED]: getAlertActions,
	[ACTION_TYPES.ALERT_ACTIONS_SUCCEEDED]: setAlertActions
};

const actions = {
	[ACTION_TYPES.ALERT_ACTIONS_REQUESTED]: {
		schema: {
			type: 'object',
			properties: {
				alertId: {type: 'string', pattern: '^[a-f0-9]{32}$'}
			},
			required: ['alertId']
		}
	}
};

export const alertActionsEffect = {
	ACTION_TYPES,
	getVariables,
	actionHandlers,
	actions
};

