import '../sn-em-ai-action-card';
import '@servicenow/now-empty-state';

const loadingState = <now-empty-state type='loading' variant='fit' widget='alert actions'/>;
const emptyState = <now-empty-state type='empty' variant='fit'/>;

const generateCardJsx = (alertId, card) => {
	return (<sn-em-ai-action-card className="ai-card-list-item" key={card.cardId} alert-id={alertId} data={card}/>);
};

const getFilteredList = (items, filter) => {
	if (filter && filter.trim()) {
		const FILTER = filter.toUpperCase();
		return items.filter(i => ~i.cardLabel.toUpperCase().indexOf(FILTER));
	} else {
		return items;
	}
};

const generateCardListJSX = (properties) => {
	const {alertId, items, filter} = properties;
	const itemsFiltered = getFilteredList(items, filter);
	const cards = itemsFiltered.map(generateCardJsx.bind(null, alertId));
	return cards.length < 1 ? emptyState : cards;

};

export const snEmAiActionCardListView = ({properties}) => {
	const {items} = properties;
	return (
		<section className="ai-card-list">
			{(items) ? generateCardListJSX(properties) : loadingState}
		</section>
	);
};
