import './components/sn-em-ai-action-header';
import './components/sn-em-ai-action-card-list';

const generatePaneJSX = (state) => {
	const {properties: {sysId}, items, cardsFilter} = state;
	return (
		<section className="ai-actions">
			<sn-em-ai-action-header className="ai-actions-header"/>
			<sn-em-ai-action-card-list className="ai-actions-content" alert-id={sysId} items={items} filter={cardsFilter}/>
		</section>
	);
};

export const snEmAiActionView = (state) => {
	return generatePaneJSX(state);
};
