import {createCustomElement} from '@servicenow/ui-core';
import {snEmAiActionHeaderView} from './snEmAiActionHeaderView';
import {CONSTANTS} from '@devsnc/library-opws-mapping-shared';
import styles from './snEmAiActionHeader.scss';

/**
 * This is a based component which displays a Title and allowing the user to enter a search query.
 *
 * ```jsx
 * <sn-em-ai-action-header ></sn-em-ai-action-header>
 * ```
 *
 * @seismicElement sn-em-ai-action-header
 * @summary The component display a header and a search query.
 */
createCustomElement('sn-em-ai-action-header', {
	view: snEmAiActionHeaderView,
	styles,
	/* dispatches is only for the use of the JS documentation */
	dispatches: {
		/**
		 * Dispatched when the user enter a filter within the search component.
		 * The payload is empty
		 * @type {{value: object}}
		 */
		'CONSTANTS.ACTION_TYPES.SEARCH_TERM_CHANGED': {}
	}
});
