import {createCustomElement} from '@servicenow/ui-core';

import {snEmAiActionCardListView} from './snEmAiActionCardListView';
import styles from './snEmAiActionCardList.scss';


/**
 * This is an experience component which retrieves all Alert's actions.
 * The last are all the actions available according to the Alert Management Rules definitions and filter
 *
 * ```jsx
 * <sn-em-ai-action-card-list alert-id='' items=[{},{}...] filter=''></sn-em-ai-action-card-list>
 * ```
 *
 * @seismicElement sn-em-ai-action-card-list
 * @summary The component display a list of available card actions per a given alert.
 */
createCustomElement('sn-em-ai-action-card-list', {
	view: snEmAiActionCardListView,
	styles,
	properties: {
		/**
		 * Specifies the context of the actions for a specific alertSysId.
		 * @type {string}
		 */
		alertId: {
			default: null,
			required: true,
			schema: {type: 'string', pattern: '^[a-f0-9]{32}$'}
		},
		/**
		 * Contains an Array of card objects each represent an Alert's action.
		 * @type {array}
		 */
		items: {
			default: null
		},
		/**
		 * An optional search term string to filter the items upon it.
		 * @type {string}
		 */
		filter: {
			default: null
		}
	},
	actionHandlers: {
	},
	actions: {
	},
	/* dispatches is only for the use of the JS documentation */
	dispatches: {
	}
});
