import '@servicenow/now-icon';
import '@servicenow/now-heading';
import '@servicenow/now-card';
import '@servicenow/now-button';
import '@devsnc/library-opws-mapping-shared/src/components/sn-opws-time-ago';
import {t} from 'sn-translate';
import {cardFooterEffect} from './actions/cardFooterEffect';
import {SYMBOLS} from '../../symbols';

const LAST_ACTIVATED = 'Last activated {0} by {1}, times activated {2}';
const __LAST_ACTIVATED_FOR_TRANSLATOR = t('Last activated {0} by {1}, times activated {2}'); //eslint-disable-line no-unused-vars this is hack: we need to extract this text for the translations.xml

const generateCardJsx = function (isRemediation, data, footerData) {
	const {
		cardId,
		cardLabel,
		cardTagline
	} = data;
	let footer = null;
	if (isRemediation) {
		if (!footerData || footerData.totalCount == 0){
			footer = <now-card-footer key={cardId}></now-card-footer>;
		} else {
			footer = (<now-card-footer className='padding-t-l' key={cardId}>
						<sn-opws-time-ago time-ago-format={LAST_ACTIVATED}
								  text-items={[footerData.createdBy, footerData.totalCount]}
								  last-updated={footerData.createdOn}/>
						</now-card-footer>);
		}

	}

	return (
		<now-card className="ai-panel-item" size="lg">
			<now-card-header
				heading={{label: cardLabel, size: 'sm', lines: 2}}
			>
			</now-card-header>
			<now-button className="expand" label={cardTagline.label} size="md" variant="secondary"></now-button>
			{footer}
		</now-card>
	);
};

export const snEmAiActionCardView = (state, {dispatch}) => {
	const {properties: {alertId, data}, footerData} = state;

	if (!data || !alertId) {
		return null;
	}

	const isRemediation = data.cardType === SYMBOLS.CARD_TYPES.REMEDIATION_WORKFLOW;
	if (isRemediation && !footerData) {
		const {cardData: {actionId}} = data;
		dispatch(cardFooterEffect.ACTION_TYPES.ALERT_ACTIONS_CARD_EXECUTION_REQUESTED, cardFooterEffect.getVariables(alertId, actionId), {stopPropagation: true});
	}

	return generateCardJsx(isRemediation, data, footerData);
};
