import {CONSTANTS, checkForError, LOGGING} from '@devsnc/library-opws-mapping-shared';
import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';

const ACTION_TYPES = {
	ALERT_ACTIONS_CARD_EXECUTION_REQUESTED: 'ALERT_ACTIONS_CARD_EXECUTION_REQUESTED',
	ALERT_ACTIONS_CARD_EXECUTION_STARTED: 'ALERT_ACTIONS_CARD_EXECUTION_STARTED',
	ALERT_ACTIONS_CARD_EXECUTION_SUCCEEDED: 'ALERT_ACTIONS_CARD_EXECUTION_SUCCEEDED',
	ALERT_ACTIONS_CARD_EXECUTION_FAILED: CONSTANTS.ACTION_TYPES.NOC_ERROR_OCCURRED
};

const GQL_QUERY = `
query getAlertExecution($queryConditions: String!) {
  GlideAggregateRecord_Query(tableName: "em_alert_management_execution", queryConditions: $queryConditions, groupBy: "management_rule", orderBy: "") {
    totalCount
    groupColumnLabel
    aggregates {
      count
      groupBy {
        value
        field
        displayValue
        _query {
          metaData: GlideRecord_Query {
            em_alert_management_execution(queryConditions: "$queryConditions^value=$$parent.value$$^ORDERBYDESCsys_created_on", pagination: {limit: 1}) {
              _results {
                sys_created_by {
                  value
                }
                sys_created_on {
                  value
                }
                sys_id {
                  value
                }
              }
            }
          }
        }
      }
    }
  }
}

`;

const getAlertExecution = createGraphQLEffect(GQL_QUERY, {
	startActionType: ACTION_TYPES.ALERT_ACTIONS_CARD_EXECUTION_STARTED,
	successActionType: ACTION_TYPES.ALERT_ACTIONS_CARD_EXECUTION_SUCCEEDED,
	errorActionType: ACTION_TYPES.ALERT_ACTIONS_CARD_EXECUTION_FAILED,
	variableList: ['queryConditions']
});


/**
 *
 * @param coeffects
 */
const setAlertExecution = coeffects => {
	LOGGING.log('setAlertExecution');

	const {
		dispatch,
		updateState,
		action: {payload}
	} = coeffects;

	const errors = checkForError({payload, dispatch, caller: 'setAlertExecution'});
	if (errors) {
		return;
	}

	let footerData = null;
	const {data: {GlideAggregateRecord_Query: {totalCount, aggregates}}} = payload;
	if (totalCount > 0) {
		const {_query: {metaData: {em_alert_management_execution: {_results}}}} = aggregates[0].groupBy[0];
		const {sys_created_by, sys_created_on} = _results[0];
		footerData = {
			totalCount,
			createdBy: sys_created_by.value,
			createdOn: sys_created_on.value
		};
	} else {
		footerData = {
			totalCount,
			createdBy: '',
			createdOn: ''
		};
	}
	updateState({footerData});
};



const getVariables = (alertId, actionId) => {
	return {
		queryConditions: `alert=${alertId}^rule_action=${actionId}`
	};
};

const actionHandlers = {
	[ACTION_TYPES.ALERT_ACTIONS_CARD_EXECUTION_REQUESTED]: getAlertExecution,
	[ACTION_TYPES.ALERT_ACTIONS_CARD_EXECUTION_SUCCEEDED]: setAlertExecution
};

const actions = {
	[ACTION_TYPES.ALERT_ACTIONS_CARD_EXECUTION_REQUESTED]: {
		schema: {
			type: 'object',
			properties: {
				queryConditions: {type: 'string'}
			},
			required: ['queryConditions']
		}
	}
};

/**
 * Effect that retrieves the footer metadata for a specific remediation inasmuch as createdBy, invocationCounter, lastActivateBy.
 *
 * @function cardFooterEffect
 * @example
 * import {cardFooterEffect} from '@sn-em-ai-action-card/cardFooterEffect';
 *
 * // Register with component action handlers:
 * {
 *     actionHandlers: {
 *			...cardFooterEffect.actionHandlers
 *     }
 * }
 *
 * @return {Object} cardInvocationEffect Descriptor
 */
export const cardFooterEffect = {
	ACTION_TYPES,
	getVariables,
	actionHandlers,
	actions
};
