import {CONSTANTS, checkForError, LOGGING} from '@devsnc/library-opws-mapping-shared';
import {createHttpEffect} from '@servicenow/ui-effect-http';
import {SYMBOLS} from '../../../symbols';
import {flowExecutionEffect} from '../../../actions/flowExecutionEffect';

const ACTION_TYPES = {
	ALERT_ACTION_REMEDIATION_INVOCATION_REQUESTED: 'ALERT_ACTION_REMEDIATION_INVOCATION_REQUESTED',
	ALERT_ACTION_REMEDIATION_INVOCATION_STARTED: 'ALERT_ACTION_REMEDIATION_INVOCATION_STARTED',
	ALERT_ACTION_REMEDIATION_INVOCATION_SUCCEEDED: 'ALERT_ACTION_REMEDIATION_INVOCATION_SUCCEEDED',
	ALERT_ACTION_REMEDIATION_INVOCATION_FAILED: CONSTANTS.ACTION_TYPES.NOC_ERROR_OCCURRED,
	ALERT_ACTION_CARD_MENU_ITEM_CLICKED: 'NOW_BUTTON#CLICKED'
};


const invokeAlertRemediation = createHttpEffect('/api/now/em_actions/startRemediationForAlert', {
	method: 'GET',
	queryParams: ['alertId', 'isSubflow', 'ciId', 'actionId', 'workflowDisplayName', 'alertManagementRule', 'ruleId', 'workflowId'],
	startActionType: ACTION_TYPES.ALERT_ACTION_REMEDIATION_INVOCATION_STARTED,
	successActionType: ACTION_TYPES.ALERT_ACTION_REMEDIATION_INVOCATION_SUCCEEDED,
	errorActionType: ACTION_TYPES.ALERT_ACTION_REMEDIATION_INVOCATION_FAILED
});


const getVariables = (card) => {
	return {
		'alertId': card.alertSysId,
		'isSubflow': card.isSubflow,
		'ciId': '',
		'actionId': card.actionId,
		'workflowDisplayName': card.workflowName,
		'alertManagementRule': card.alertManagementRule,
		'ruleId': card.rule,
		'workflowId': card.workflowId
	};
};

const launchApplication = cardMenuItems => {
	const {cardData: {url}} = cardMenuItems;
	if (url)
		window.open(url);
	else
		LOGGING.error('launchApplication failed the URL is empty');
};

const invokeRemediation = (data, dispatch) => {
	LOGGING.info('cardInvocationEffect', {
		caller: 'invokeRemediation',
		extra: {data}
	});
	const params = getVariables(data.cardMenuItems[0]);
	dispatch(ACTION_TYPES.ALERT_ACTION_REMEDIATION_INVOCATION_REQUESTED, params, {stopPropagation: true});
};


/**
 * Perform all the tasks needed to do after the remediation started to invoke
 * @param coeffects
 */
const handleAlertRemediationResult = coeffects => {
	const {
		dispatch,
		properties: {data},
		action: {payload}
	} = coeffects;
	const errors = checkForError({payload, dispatch, caller: 'handleAlertRemediationResult'});
	if (errors) {
		LOGGING.error('handleAlertRemediationResult failed', errors);
		return;
	}

	const {cardData: {isSubflow}} = data;
	const {result: {taskId}} = payload;
	LOGGING.info('cardInvocationEffect', {
		caller: 'handleAlertRemediationResult',
		extra: {isSubflow, taskId}
	});
	dispatch(flowExecutionEffect.ACTION_TYPES.FLOW_EXECUTION_INVOCATION_STARTED, {taskId});

	//Open flow execution
	const url = (isSubflow) ? `/$flow-designer.do?sysparm_nostack=true#/operations/context/${taskId}` : `/context_workflow.do?sysparm_sys_id=${taskId}`;
	window.open(url);
};

/**
 * A factory which handle any clicks on a card menu item
 * @param coeffects
 */
const cardMenuItemClicked = coeffects => {
	const {
		dispatch,
		properties: {data},
	} = coeffects;
	const {cardType} = data;

	if (cardType === SYMBOLS.CARD_TYPES.REMEDIATION_WORKFLOW)
		invokeRemediation(data, dispatch);
	else
		launchApplication(data);
};


const actionHandlers = {
	[ACTION_TYPES.ALERT_ACTION_CARD_MENU_ITEM_CLICKED]: cardMenuItemClicked,
	[ACTION_TYPES.ALERT_ACTION_REMEDIATION_INVOCATION_REQUESTED]: invokeAlertRemediation,
	[ACTION_TYPES.ALERT_ACTION_REMEDIATION_INVOCATION_SUCCEEDED]: handleAlertRemediationResult
};

const actions = {
	[ACTION_TYPES.ALERT_ACTION_CARD_MENU_ITEM_CLICKED]: {
		schema: {
			type: 'object',
			properties: {
				queryConditions: {type: 'string'}
			},
			required: ['queryConditions']
		}
	}
};

/**
 * Effect that handles both remediation / launch-application actions and dispatches actions accordingly:
 *  - remediation: invoke the flow designer sub flow with the specific job
 *  - launch-application: Open a new window with the specific custom URL
 *
 * @function cardInvocationEffect
 * @example
 * import {cardInvocationEffect} from '@sn-em-ai-action-card/cardInvocationEffect';
 *
 * // Register with component action handlers:
 * {
 *     actionHandlers: {
 *			...cardInvocationEffect.actionHandlers
 *     }
 * }
 *
 * @return {Object} cardInvocationEffect Descriptor
 */
export const cardInvocationEffect = {
	ACTION_TYPES,
	getVariables,
	actionHandlers,
	actions
};
