import {createCustomElement} from '@servicenow/ui-core';

import {snEmAiActionView} from './snEmAiActionView';
import styles from './snEmAiAction.scss';
import {SYMBOLS} from './symbols';
import {alertActionsEffect} from './actions/alertActionsEffect';
import {flowExecutionEffect} from './actions/flowExecutionEffect';

/**
 * This component presents a list of available actions inasmuch as remediation, launch applications and more, which a user can opt for a specific given alert
 * Thereof component is displayed in the right panel within the Workspace Alert intelligent
 *
 * ```jsx
 * <sn-em-ai-action sys-id=''></sn-em-ai-action>
 * ```
 *
 * @seismicElement sn-em-ai-action
 * @summary The component display a list of available actions per a given alert.
 */
createCustomElement('sn-em-ai-action', {
	view: snEmAiActionView,
	styles,
	initialState: {
		componentState: SYMBOLS.COMPONENT_STATE.BOOTSTRAP,
		items: null,
		cardsFilter: null
	},
	properties: {
		/**
		 * Specifies the context of the actions for a specific alertSysId.
		 * @type {string}
		 */
		sysId: {
			default: null,
			required: true,
			schema: {type: 'string', pattern: '^[a-f0-9]{32}$'}
		}
	},
	actionHandlers: {
		...alertActionsEffect.actionHandlers,
		...flowExecutionEffect.actionHandlers
	},
	actions: {
		...alertActionsEffect.actions,
		...flowExecutionEffect.actions
	},
	/* dispatches is only for the use of the JS documentation */
	dispatches: {
		/**
		 * Dispatched when the alert pane component is loaded on the first time.
		 * The payload is empty
		 * @type {{value: object}}
		 */
		[alertActionsEffect.ACTION_TYPES.ALERT_ACTIONS_REQUESTED]: {}
	}
});
