import {LOGGING} from '@devsnc/library-opws-mapping-shared';
import {createAmbSubscriptionEffect} from '@servicenow/ui-effect-amb';
import {SYMBOLS} from '../symbols';
import {alertActionsEffect} from './alertActionsEffect';
import {actionTypes} from '@servicenow/ui-core';
const {COMPONENT_DISCONNECTED} = actionTypes;

const ACTION_TYPES = {
	FLOW_EXECUTION_SUBSCRIPTION_CHANGED: 'FLOW_EXECUTION#SUBSCRIPTION_CHANGED',
	FLOW_EXECUTION_SUBSCRIBE_STARTED: 'FLOW_EXECUTION#SUBSCRIBE_STARTED',
	FLOW_EXECUTION_SUBSCRIBE_FAILED: 'FLOW_EXECUTION#SUBSCRIBE_FAILED',
	FLOW_EXECUTION_SUBSCRIBED: 'FLOW_EXECUTION#SUBSCRIBED',
	FLOW_EXECUTION_UNSUBSCRIBED: 'FLOW_EXECUTION#UNSUBSCRIBED',
	FLOW_EXECUTION_ITEM_CHANGED: 'FLOW_EXECUTION#ITEM_CHANGED',
	FLOW_EXECUTION_INVOCATION_STARTED: 'FLOW_EXECUTION#INVOCATION_STARTED'
};

const getVariables = (taskId, subscribe) => {
	const table = 'sys_flow_context';
	const condition = `sys_id=${taskId}`;
	const filter = btoa(condition);
	return {
		table,
		filter,
		subscribe
	};
};

const itemChangedHandler = coeffects => {
	const {
		dispatch,
		properties: {sysId},
		action: {payload: {data}}
	} = coeffects;

	const {sys_id: taskId, record: {state: {value}}} = data;
	LOGGING.info('flowExecutionEffect', {
		caller: 'itemChangedHandler',
		extra: {taskId, value}
	});

	if (value == 'COMPLETE') {
		//Unsubscribe from AMB for this specific taskId
		dispatch(ACTION_TYPES.FLOW_EXECUTION_SUBSCRIPTION_CHANGED, getVariables(taskId, false));
		//Refresh the action list with the updated data
		dispatch(alertActionsEffect.ACTION_TYPES.ALERT_ACTIONS_REQUESTED, alertActionsEffect.getVariables(sysId), {stopPropagation: true});
	}
};

const waitTillCompleted = coeffects => {
	LOGGING.log('waitTillCompleted-> wait till Flow Execution is finished');
	const {
		dispatch,
		updateState,
		action: {payload: {taskId}}
	} = coeffects;

	LOGGING.info('flowExecutionEffect', {
		caller: 'waitTillCompleted',
		extra: {taskId}
	});

	//Subscribe to AMB for this specific taskId
	dispatch(ACTION_TYPES.FLOW_EXECUTION_SUBSCRIPTION_CHANGED, getVariables(taskId, true));

	const items = null;
	updateState({items, componentState: SYMBOLS.COMPONENT_STATE.PENDING});
};

const flowExecutionHandler = coeffects => {
	const {action: {type}} = coeffects;
	LOGGING.info('flowExecutionEffect', {
		caller: 'flowExecutionHandler',
		extra: {type}
	});
};


/**
 * Dynamic Channel ID - :table & :filter must be provided at runtime when subscribing
 */
const flowExecutionRecordWatcherHandler = createAmbSubscriptionEffect('/rw/default/:table/:filter', {
	subscribeStartedActionType: ACTION_TYPES.FLOW_EXECUTION_SUBSCRIBE_STARTED,
	subscribeSucceededActionType: ACTION_TYPES.FLOW_EXECUTION_SUBSCRIBED,
	subscribeFailedActionType: ACTION_TYPES.FLOW_EXECUTION_SUBSCRIBE_FAILED,
	unsubscribeSucceededActionType: ACTION_TYPES.FLOW_EXECUTION_UNSUBSCRIBED,
	messageReceivedActionType: ACTION_TYPES.FLOW_EXECUTION_ITEM_CHANGED
});


const actionHandlers = {
	[COMPONENT_DISCONNECTED]: ({dispatch})=> dispatch(ACTION_TYPES.FLOW_EXECUTION_SUBSCRIPTION_CHANGED, getVariables('123', false)),
	[ACTION_TYPES.FLOW_EXECUTION_SUBSCRIPTION_CHANGED]: flowExecutionRecordWatcherHandler,
	[ACTION_TYPES.FLOW_EXECUTION_SUBSCRIBE_STARTED]: flowExecutionHandler,
	[ACTION_TYPES.FLOW_EXECUTION_SUBSCRIBE_FAILED]: flowExecutionHandler,
	[ACTION_TYPES.FLOW_EXECUTION_SUBSCRIBED]: flowExecutionHandler,
	[ACTION_TYPES.FLOW_EXECUTION_UNSUBSCRIBED]: flowExecutionHandler,
	[ACTION_TYPES.FLOW_EXECUTION_ITEM_CHANGED]: itemChangedHandler,
	[ACTION_TYPES.FLOW_EXECUTION_INVOCATION_STARTED]: waitTillCompleted
};

const actions = {
	[ACTION_TYPES.FLOW_EXECUTION_SUBSCRIPTION_CHANGED]: {
		schema: {
			type: 'object',
			properties: {
				table: {type: 'string'},
				filter: {type: 'string'},
				subscribe: {type: 'boolean'}
			},
			required: ['table','filter','subscribe']
		}
	}
};

export const flowExecutionEffect = {
	ACTION_TYPES,
	actionHandlers,
	actions
};
