import {createCustomElement} from '@servicenow/ui-core';

import {snEmAiActionCardView} from './snEmAiActionCardView';
import styles from './snEmAiActionCard.scss';
import {cardFooterEffect} from './actions/cardFooterEffect';
import {cardInvocationEffect} from './actions/cardInvocationEffect';

/**
 * This is an experience component which represents a specific type of Alert Action launch application or remediation
 * For the last it will also retrieves and display further info on he action inasmuch as owner,last activated date and the number of times it ws activate
 *
 * ```jsx
 * <sn-em-ai-action-card data={} alert-id=''></sn-em-ai-action-card>
 * ```
 *
 * @seismicElement sn-em-ai-action
 * @summary The component display a card with a specific alert actions details.
 */
createCustomElement('sn-em-ai-action-card', {
	view: snEmAiActionCardView,
	styles,
	properties: {
		/**
		 * Specifies the context of the actions for a specific alertSysId.
		 * @type {string}
		 */
		alertId: {
			default: null,
			required: true,
			schema: {type: 'string', pattern: '^[a-f0-9]{32}$'}
		},
		/**
		 * Specifies the context of the actions for a specific alertSysId.
		 * @type {object} Card object which encapsulate all card configuration
		 */
		data: {
			default: null,
			required: true,
			schema: {type: 'object'}
		}
	},
	actionHandlers: {
		...cardFooterEffect.actionHandlers,
		...cardInvocationEffect.actionHandlers,
	},
	actions: {
		...cardFooterEffect.actions,
		...cardInvocationEffect.actions
	},
	eventHandlers: [],
	/* dispatches is only for the use of the JS documentation */
	dispatches: {

	}
});

